body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-size: 14px;
}
.App {
  padding: 16px;
}

.flex {
  display: flex;
}
.justify-between {
  justify-content: space-between;
}

.justify-end {
  justify-content: flex-end;
}

.items-center {
  align-items: center;
}

.pa-md {
  padding: 16px;
}
.px-md {
  padding-left: 16px;
  padding-right: 16px;
}
.pa-sm {
  padding: 12px;
}
.pa-xs {
  padding: 8px;
}
.py-xs {
  padding-top: 8px;
  padding-bottom: 8px;
}
.pl-sm {
  padding-left: 12px;
}

.my-md {
  margin-top: 16px;
  margin-bottom: 16px;
}
.mt-2xs {
  margin-top: 4px;
}
.cur-pointer {
  cursor: pointer;
}
.border-radius-xs {
  border-radius: 4px;
}
.border-slate-1 {
  border: 1px solid #eee;
}
.box-shadow-slate {
  box-shadow: 0px 0px 4px #ccc;
}
.text-bold {
  font-weight: bold;
}
.flex-grow-1 {
  flex-grow: 1;
}
.border-red {
  border-color: red;
}
.border-warning {
  border-color: orange;
}
.box-shadow-red {
  box-shadow: 0px 0px 4px red;
}
.full-width {
  width: 100%;
}
.text-red {
  color: red;
}
.font-size-sm {
  font-size: 12px;
}
.text-right {
  text-align: right;
}
.text-slate-4 {
  color: #999;
}
.text-slate-11 {
  color: #333;
}
.overflow-hidden {
  overflow: hidden;
}
